.home {
  background-image: url(../assets/food.jpeg);
  background-repeat: no-repeat;
  height: 90vh;
  background-size: cover;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  background-position: center;
  display: flex;
  flex-direction: column;
  align-items: left;
}
.header-container {
  width: auto;
  margin-left: 50px;
}
.header-container h1 {
  justify-content: center;
  align-items: center;
  font-size: 90px;
  height: 60px;
  font-weight: 50;
}
.header-container p {
  padding: 0;
  font-size: 40px;
  font-weight: lighter;
}
.header-container button {
  border: none;
  border-radius: 5px;
  color: white;
  background-color: #121619;
  cursor: pointer;
  height: 50px;
  width: 180px;
  justify-content: center;
  text-decoration: none;
  font-size: 20px;
}
.header-container button:hover {
  background-color: #07090a;
  cursor: pointer;
  transition: 0.3s ease-in;
}
@media only screen and (max-width:650px) {
    .home{
        justify-content: center;
        align-items: center;
    }
    .header-container{
        margin-left: 0px;
        border-radius: 10px;
        padding: 10px;
        width:80%;
        display:flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        background-color: #121619;
        color: white;
    }
    .header-container h1{
        font-size: 40px;
        height: 30px;
        color: white;
    }
    .header-container p{
      font-size: 30px;
      color: white;
    }
    .header-container  button{
        background-color: white;
        color: #121619;
        margin-bottom: 30px;
    }
    .header-container button:hover{
        background-color: white;
        color: #121619;
    }
}
